.primary{--tw-text-opacity:1;color:rgb(17 24 39 / var(--tw-text-opacity, 1));background:linear-gradient(90deg, #2193b0 0%, #6dd5ed 50%, #2193b0 100%),linear-gradient(0deg, #ffffff, #ffffff);background-size:200% 100%}.primary:hover{background-position:100% 0}.outline{border-width:2px;--tw-border-opacity:1;border-color:rgb(255 255 255 / var(--tw-border-opacity, 1))}.outline:hover{--tw-bg-opacity:1;background-color:rgb(255 255 255 / var(--tw-bg-opacity, 1));--tw-text-opacity:1;color:rgb(17 24 39 / var(--tw-text-opacity, 1));opacity:0.9}.white{--tw-bg-opacity:1;background-color:rgb(255 255 255 / var(--tw-bg-opacity, 1));--tw-text-opacity:1;color:rgb(17 24 39 / var(--tw-text-opacity, 1))}.white:hover{opacity:0.9}